import React from "react";
import "./login.css";

// let lista;
const avatars = [
	[
		{id:1}
	],
	 [
		{id:2}
	 ]

];

export const Perfiles = ({ history }) => {



	const handleDashboard = () => {
		history.replace("/dashboard");
	};






	return (
		<div className="row ">
			<div className="col-sm-3"></div>
			<div className="col-sm-4 top left">
				<h3>Quien aprende hoy?</h3>
				<div className="card-group ">
					<div className=''>{
					(avatars.map((avatar)=>{

					return <button key={avatar.id} 
					className="avatarPerfil2 " 
					type="button"  
					onClick={handleDashboard}>

					</button>
					}
					))
				}</div>
				</div>
				<br/>
				<br/>
				<button 
				onClick={handleDashboard}
				className="btn  btn-outline-warning btn-lg btn-block" 
				>
				Siguiente
				</button>
			</div>

			<div className="col-sm-7"></div>
		</div>
	);
};
