import React, { useState } from "react";
import "../../Views/Login/login.css";

export const Intereses = ({ history }) => {
	const [intereses, setIntereses] = useState([]);

	//vcariables
	let usuario = "Sergio";
	const saludo = `!Bienvendido ${usuario}!`;
	const handleDashboard = () => {
		history.replace("/dashboard");
	};

	// const handleGoBack = () => {
	// 	history.replace("/formRegistro");
	// };

	const handleInteresList = (e) => {
		e.preventDefault();
		let valorBoton = e.target.value;
		console.log(`cliqueaste el boton  ${valorBoton}`);
		console.log(`arreglo con lo valores  ${intereses}`);

		setIntereses([...intereses, valorBoton]);
		console.log(intereses);
	};

	return (
		<div className="row ">
			<div className="col-sm-4"></div>
			<div className="col-sm-4 centrador">
				<h1 className="saludo">{saludo}</h1>
				<h6>Para una mejor experiencia en tu plataforma, selecciona tus temas de interés</h6>

				<button type="button" onClick={handleInteresList} value="ofimatica" className="btn btn-outline-primary espaciador-botones">
					Ofimatica
				</button>
				<button type="button" onClick={handleInteresList} value="Finanzas y contabilidad" className="btn btn-outline-secondary espaciador-botones">
					Finanzas y contabilidad
				</button>
				<button type="button" onClick={handleInteresList} value="Comunicacion" className="btn btn-outline-success espaciador-botones">
					Comunicacion
				</button>
				<button type="button" onClick={handleInteresList} value="Informacion y softwares" className="btn btn-outline-danger espaciador-botones">
					Informacion y software
				</button>
				<button value="Diseño" onClick={handleInteresList} type="button" className="btn btn-outline-warning espaciador-botones">
					Diseño
				</button>
				<button value="Inteligencia en ventas" onClick={handleInteresList} type="button" className="btn btn-outline-info espaciador-botones">
					Inteligencia en ventas
				</button>
				<button value="Marketing" type="button" onClick={handleInteresList} className="btn btn-outline-danger	 espaciador-botones">
					Marketing
				</button>
				<button value="Desarroollo personal" onClick={handleInteresList} type="button" className="btn btn-outline-info espaciador-botones">
					Desarroollo personal
				</button>
				<button value="Literatura" type="button" onClick={handleInteresList} className="btn btn-outline-dark espaciador-botones">
					Literatura
				</button>
				<br />
				<br />

				<button onClick={handleDashboard} className="btn  btn-outline-warning btn-lg btn-block">
					Siguiente
				</button>
			</div> 

			<div className="col-sm-4"></div>
		</div>
	);
};
