import React, { useState } from "react";
import { Redirect } from "react-router-dom";

export const Registro = () => {
	const [formValues, setFormValues] = useState({
		folio: "",
		correo: "",
		acceso: false,
	});

	const handelRegistration = () => {
		setFormValues({ ...formValues, acceso: true });
	};

	return (
		<div className="container">
			<div className="iniciarSesion">
				<img src="../assets/images/avatar.png" className="rounded mx-auto d-block color" alt="" />
				<div className="text-center">
					<div className="avatar container-fluid"></div>
					<h3>Registrate</h3>
				</div>

				<form onSubmit={handelRegistration}>
					<div className="form-group">
						<div className="input-group flex-nowrap">
							<div className="input-group-prepend espaciador">
								<span className="input-group-text">
									<i className="material-icons">book</i>
								</span>
							</div>
							<input type="number" className="form-control is valid espaciador" id="exampleInputPassword1" placeholder="Folio" name="folio" />
						</div>
						<div className="input-group flex-nowrap">
							<div className="input-group-prepend espaciador">
								<span className="input-group-text i">
									<i className="material-icons">contact_mail</i>{" "}
								</span>
							</div>
							<input
								type="email"
								className="form-control espaciador"
								id="exampleInputEmail1"
								aria-describedby="emailHelp"
								placeholder="Correo"
								name="usuario"
								// value={usuario}
								// onChange={handleInputchange}
							/>
						</div>
					</div>

					<div className="form-group form-check"></div>
					<button type="submit" className="btn btn-outline-warning btn-lg btn-block">
						Ingresar
					</button>
				</form>
				{formValues.acceso ? <Redirect to="/formRegistro" /> : null}
				<br />

				<button type="button" className="btn btn-light btn-lg btn-block">
					olvide mi contraseña
				</button>
			</div>
			<br />
		</div>
	);
};
