import React from "react";
import "./containers.css";
import SideNav, { NavItem, NavIcon, NavText } from "@trendmicro/react-sidenav";
import "@trendmicro/react-sidenav/dist/react-sidenav.css"
import { BrowserRouter as Router,  Route } from "react-router-dom";
import { Dashboard } from "./Dashboard";
import { Intereses } from "../components/Forms/InteresesUsuario";

export const SideBar = () => {
	return (
		<div>
			<SideNav
				onSelect={(selected) => {
					console.log("presion :D");
				}}
			>
				<SideNav.Toggle />
				<SideNav.Nav defaultSelected="opc1">
					<NavItem eventKey="opc 1">
						<NavIcon>
							<i className="fa fa-fw fa-home" style={{ fontSize: "1.75em" }} />
						</NavIcon>
						<NavText>Dashboard</NavText>
					</NavItem>
					<NavItem eventKey="opc 2">
						<NavIcon>
							<i className="fa fa-fw fa-line-chart" style={{ fontSize: "1.75em" }} />
						</NavIcon>
						<NavText>Cursos</NavText>
						<NavItem eventKey="charts/linechart">
							<NavText>Rutas de Aprendizaje</NavText>
						</NavItem>
						<NavItem eventKey="charts/barchart">
							<NavText>otra ocpion</NavText>
						</NavItem>
					</NavItem>
				</SideNav.Nav>
			</SideNav>
			<Router>
				<Route
					render={({ location, history }) => (
						<>c
							<SideNav
								onSelect={(selected) => {
									const to = "/" + selected;
									if (location.pathname !== to) {
										history.push(to);
									}
								}}
							>
								<SideNav.Toggle />
								<SideNav.Nav defaultSelected="home">
									<NavItem eventKey="dashboard">
										<NavIcon>
                                        <i className="material-icons" style={{ fontSize: "1.75em" }}>book</i>
										</NavIcon>
										<NavText>Dashboard</NavText>
									</NavItem>
									<NavItem eventKey="login">
										<NavIcon>
                                        <i className="material-icons" style={{ fontSize: "1.75em" }}>book</i>
										</NavIcon>
										<NavText>Login</NavText>
									</NavItem>
								</SideNav.Nav>
							</SideNav>





							<main>
								<Route path="/1" exact component={<Dashboard />} />
								<Route path="/2" component={<Intereses />} />
								<Route path="/3" component={<Intereses />} />
							</main>
						</>
					)}
				/>
			</Router>
		</div>
	);
};
