import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";

import { Intereses } from "../components/Forms/InteresesUsuario";
import { FormRegistroUsuario } from "../components/Forms/FormRegistroUsuario";
import { Dashboard } from "../Containers/Dashboard";
import { Login } from "../Views/Login/Login";
import { Perfiles } from "../Views/Login/Perfiles";

export const DashboardRoutes = () => {
	return (
		<div>
			<Switch>
				{/* Rutas Publicas */}
				<Route exact path="/formRegistro" component={FormRegistroUsuario} />
				<Route exact path="/Login" component={Login} />
				{/* Rutas Privadas */}
				<Route exact path="/dashboard" component={Dashboard} />
				<Route exact path="/Intereses" component={Intereses} />
				<Route exact path="/Intereses" component={Intereses} />
				<Route exact path="/Perfiles" component={Perfiles} />
				{/* Ruta por defecto  */}
				<Redirect to="/Login" />
			</Switch>
		</div>
	);
};
