import React, { useState } from "react";
import "./login.css";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { Redirect } from "react-router-dom";


export const IniciarSesion = () => {
	//States
	const [isAutenficated, setIsAutenficated] = useState(false);
	const [formValues, setFormValues] = useState({
		usuario: "",
		password: "",
		campoUsuario: false,
		campoPassword: false,
	});

	//destructuring XD
	const { campoUsuario, campoPassword, usuario, password } = formValues;

	/**
	 * Metodo encargado de validar credenciales y
	 * dar acceso al dashboard
	 * @param {*} e
	 */
	const handleAcceso = (e) => {
		e.preventDefault();
		//validacion de campos
		console.log("presionaste el boton");
		if (usuario.length > 0 && password.length > 0) {
			console.log("campos amyores a 0s");
		}
		setIsAutenficated(true);

		//consultar informacion de la BD

		//validacion de usuario

		//acceso de login
		console.log("redirigiendo a dashboard");
		if (isAutenficated) {
			Swal.fire({
				position: "top-end",
				icon: "success",
				title: "acceso concedido",
				showConfirmButton: false,
				timer: 1500,
			});
		}
	};

	/**
	 * metodo encargado de recopilar informacion de los input y
	 * guardarlos en la variables local de estado formValues
	 * @param {*} e
	 */
	const handleInputchange = (e) => {
		e.preventDefault();
		setFormValues({
			...formValues,
			[e.target.name]: e.target.value,
		});
		let longituCampo = e.target.value.length;
		console.log(longituCampo);
		// bug de campos
	};

	return (
		<div className="container">
			<div className="iniciarSesion">
				<img src="../assets/images/avatar.png" className="rounded mx-auto d-block color" alt="" />
				<div className="text-center">
					<div className="avatar container-fluid"></div>
					<h3>Iniciar Sesión</h3>
				</div>

				<form onSubmit={handleAcceso}>
					<div className="form-group">
						<div className="input-group flex-nowrap">
							<div className="input-group-prepend espaciador">
								<span className="input-group-text">
									<i className="material-icons">assignment_ind</i>
								</span>
							</div>
							<input
								type="email"
								className={` form-control espaciador  ${campoUsuario ? "is-valid" : "is-valid"}`}
								placeholder="Usuario"
								name="usuario"
								value={usuario}
								onChange={handleInputchange}
							/>
						</div>
						<div className="input-group flex-nowrap">
							<div className="input-group-prepend espaciador">
								<span className="input-group-text i" id="addon-wrapping">
									<i className="material-icons">https</i>{" "}
								</span>
							</div>
							<input
								type="password"
								className={` form-control espaciador is-valid ${campoPassword ? "is-valid" : "is-valid"}`}
								placeholder="Contraseña"
								value={password}
								name="password"
								onChange={handleInputchange}
							/>
						</div>
					</div>

					<div className="form-group form-check"></div>
					<button type="submit" className="btn btn-outline-warning btn-lg btn-block">
						Ingresar
					</button>
					
					{
						
						isAutenficated? 
						
						<Redirect to='/perfiles'/> :null
					}
				
				</form>
				<br />

				<button type="button" className="btn btn-light btn-lg  btn-block">
					olvide mi contraseña
				</button>
			</div>
			<br />
		</div>
	);
};
