import React, { useState } from "react";
import Swal from "sweetalert2/dist/sweetalert2.js";

export const FormRegistroUsuario = ({ history }) => {
	const [formValues, setFormValues] = useState({
		nombres: "",
		apellidos: "",
		email: "",
		password: "",
		puestoTrabajo: "",
	});

	//destructuring XD
	const { nombres, apellidos, email, password, puestoTrabajo } = formValues;
	const nombreEmpresa = "Yo habia ponido el nombre de la empresa aqui";
	// const ponido = "https://plantillasdememes.com/img/plantillas/yo-habia-ponido-mis-cosas-aqui01561772726.png";
	

	const handleGoBack = () => {
		history.replace("/Login");
	};

	const handleNewRegister = () => {
		Swal.fire({
			position: "top-end",
			icon: "success",
			title: "Registro Exitoso",
			showConfirmButton: false,
			timer: 1500,
		});
	};

	const handleInputchange = (e) => {
		e.preventDefault();
		setFormValues({
			...formValues,
			[e.target.name]: e.target.value,
		});
	};
	
	const handelRegistration = (e) => {
		e.preventDefault()
		history.replace("/Intereses");
	}
	

	return (
		<div className="row ">
			<div className="col-sm-4"></div>
			<div className="col-sm-4">
				{nombreEmpresa ? (
					<div>
						<h3>{nombreEmpresa}</h3>
						<div className="gatito container-fluid"></div>
						<p>"Numero de registro /el limite son las licencias compradas"</p>
					</div>
				) : (
					<div>
						<div className="gatito container-fluid">
							<h3>{nombreEmpresa}</h3>
						</div>
					</div>
				)}

				<form onSubmit={handleNewRegister}>
					<div className="form-group">
						<div className="input-group flex-nowrap">
							<div className="input-group-prepend espaciador">
								<span className="input-group-text">
									<i className="material-icons">assignment_ind</i>
								</span>
							</div>
							<input type="text" className={` form-control espaciador `} placeholder="Nombre(s)" name="nombres" value={nombres} onChange={handleInputchange} />
						</div>
						<div className="input-group flex-nowrap">
							<div className="input-group-prepend espaciador">
								<span className="input-group-text i" id="addon-wrapping">
									<i className="material-icons">https</i>
								</span>
							</div>
							<input type="text" className={` form-control espaciador `} placeholder="Apellido(s)" value={apellidos} name="apellidos" onChange={handleInputchange} />
						</div>
						
						<div className="input-group flex-nowrap">
							<div className="input-group-prepend espaciador">
								<span className="input-group-text i" id="addon-wrapping">
									<i className="material-icons">https</i>
								</span>
							</div>
							<input type="email" className={` form-control espaciador `} placeholder="Correo Electronico" value={email} name="email" onChange={handleInputchange} />
						</div>
						<div className="input-group flex-nowrap">
							<div className="input-group-prepend espaciador">
								<span className="input-group-text i" id="addon-wrapping">
									<i className="material-icons">https</i>
								</span>
							</div>
							<input type="password" className={` form-control espaciador `} placeholder="Contraseña" value={password} name="password" onChange={handleInputchange} />
						</div>
						<div className="input-group flex-nowrap">
							<div className="input-group-prepend espaciador">
								<span className="input-group-text i" id="addon-wrapping">
									<i className="material-icons">https</i>{" "}
								</span>
							</div>
							<input type="text" className={` form-control espaciador `} placeholder="Puesto de Trabajo" value={puestoTrabajo} name="puestoTrabajo" onChange={handleInputchange} />
						</div>
					</div>

					<h6>Selcciona tu foto de perfil</h6>

					<div class="custom-file">
						<input type="file" class="custom-file-input" id="customFileLang" lang="es" />
						<label class="custom-file-label" for="customFileLang">
							Seleccionar Archivo
						</label>
					</div>
					<br />
					<br />
					<button type="submit" className="btn btn-outline-warning btn-lg btn-block"
					onClick={handelRegistration}
					>
						Siguiente
					</button>
				</form>

				<button onClick={handleGoBack} className="btn btn-info">
					{/* <i className="material-icons"> keyboard_backspace</i> */}
					Regresar
				</button>
			</div>
			<div className="col-sm-4"></div>
		</div>
	);
};
